import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService, LoginSchema, LanguageService } from 'src/app/services';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  public region: string;
  constructor(
    private readonly authenticationService: AuthenticationService,
    private readonly router: Router,
    private readonly languageService: LanguageService
  ) {
    this.region = languageService.estimatedRegion;
  }

    public async login(schema: LoginSchema) {
    const result = await this.authenticationService.login(schema, this.region, this.languageService.language);
    if (result) {
      this.router.navigate([environment.mainPage]);
    }
  }

  ngOnInit() {
  }

}
